.title{
    font-size: 50px;
    text-align: center;;
    width: 100%;
    height: 100px;
    margin-top: 100px;
    color: white;
}


.mycard{
    background-color: darkred;
    text-align: center;
    height: 80px;
    color: white;
    margin: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.headCard{
    background-color: darkred;
    text-align: center;
    color: white;
}


.myrow{
    padding-top: 100px;
}

@media screen and (max-width: 769px) {
    .myrow{
        padding-top: 0px;
    }
}

.deleteButton{
    background-color: darkred;
    color: white;
    border: none;
    border-radius: 5px;
    border: 1px solid white;
    padding: 5px;
    margin: 5px;
}